* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #222222;
}

header h1 {
  color: white;
  font-size: 2rem;
  text-align: center;
  padding: 50px;
}

.movie_container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
}

.movie {
  padding: 30px;
}

.movie__title {
  font-size: 1.5rem;
  color: rgb(238, 236, 236);
}

.movie__year {
  font-size: 1rem;
  color: gray;
}

.form__group {
  position: relative;
  padding: 15px 0 0;

  width: 50%;
  margin: 10px auto;
}
.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.form__label {
  position: absolute;
  left: 0;
  bottom: 0.2rem;
  color: #ccc;
  pointer-events: none;
  transition: all 0.5s;
  font-size: 1.5rem;
}

.form__field:valid ~ .form__label,
.form__field:focus ~ .form__label {
  color: #11998e;
  transition: all 0.5s;
  bottom: 2.5rem;
  font-size: 1rem;
}

.form__field:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 10%;
}
